import './App.css';
import { Outlet, useRoutes } from 'react-router-dom';
import Dashboard from './components/layout/dashboard';
import DatabaseTable from './components/pages/Tables';

function App() {
  const routes = useRoutes([
    {
      path: '/',
      element:
        <Dashboard>
          <Outlet />
        </Dashboard>,
      children: [
        {
          path: '/',
          element: <DatabaseTable />
        }
      ]
    }
  ])

  return (
    <>
      {
        routes
      }
    </>
  );
}

export default App;
