import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import axios from 'axios'
import { Button, Typography } from '@mui/material';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import { host } from '../../variables';

export default function DatabaseTable() {

    const [rows, setRow] = React.useState([])
    const [header, setHeader] = React.useState({})

    React.useEffect(() => {
        try {
            console.log('data', process.env.host, process.env.NODE_ENV)
            fetchData();
        } catch (e) {
            console.error(e)
        }
    }, [])

    async function fetchData() {
        const res = await axios.get(`${host}/list_tables`)
        console.log(res.data)
        res.data[0] === undefined ? setHeader({}) :
            setHeader(res.data[0])
        setRow(res.data)
    }

    async function Delete(name) {
        console.log('data', name)
        try {
            await axios.delete(`${host}/delete_table`, { data: { table_name: name } })
            fetchData()
        } catch (e) {
            console.error('data', e)
        }
    }

    return (
        <TableContainer component={Paper} sx={{ p: 5 }}>
            {
                <Typography>{process.env.dev ? 'host' : process.env.dev}</Typography>
            }
            {
                rows.length > 0 ?
                    <Table sx={{ textAlign: 'center' }} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                {
                                    Object.keys(header).map((item, id) => {
                                        return (
                                            <TableCell key={id} sx={{
                                                textAlign: 'center'
                                            }}>
                                                <Button>
                                                    {item}
                                                </Button>
                                            </TableCell>
                                        )
                                    })
                                }
                                <TableCell sx={{
                                    textAlign: 'center'
                                }}>
                                    <Button>
                                        Options
                                    </Button>
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map((row, id) => (
                                <TableRow
                                    key={id}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row" sx={{
                                        textAlign: 'center'
                                    }}>
                                        <Button disabled>
                                            <Typography color={'grey'} fontSize={'13px'}>
                                                {row.table_name}
                                            </Typography>
                                        </Button>
                                    </TableCell>
                                    <TableCell sx={{
                                        textAlign: 'center'
                                    }}>
                                        <Button onClick={() => Delete(row.table_name)} >
                                            <DeleteOutlineIcon />
                                        </Button>
                                        <Button>
                                            <AddCircleOutlineIcon />
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                    :
                    <Button>No Tables Found !</Button>
            }
        </TableContainer>
    );
}